      <h1 class="content_title">微信隐私保护指引</h1>
    </div>
    <div class="content_body">
      <p>更新日期：2021年10月29日<br> 生效日期：2021年10月29日<br></p>
      <p><strong>为更充分保障你的权利，我们对《微信隐私保护指引》进行了更新，此版本的更新主要集中于：</strong></p>
      <p>1. 在第5章节“你的权利”部分，补充了部分功能，增加了个人信息浏览和导出机制，设置了系统权限和授权管理入口等。</p>
      <p>2. 在第1章节“我们收集、使用的信息”部分，更详细地披露了视频号、公众号、看一看、搜一搜个性化推荐或展示机制，并增加了个性化自主控制途径。</p>
      <p>3. 在第1章节“我们收集、使用的信息”部分，进一步披露了我们在为你提供视频号直播、互动等功能时如何处理你的个人信息，以及你享有的互动历史查阅、视频号个人信息浏览与导出等权利。</p>
      <p>4. 在第4章节“信息共享”部分，分别增加了<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=ad_shared_info_list&amp;lang=zh_CN">《广告信息共享清单》</a>以进一步披露我们为优化广告效果，如何与第三方共享经去标识化或匿名化处理后的信息，以及<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=3rdsdk_list&amp;lang=zh_CN">《第三方SDK目录》</a>以更充分的披露我们为确保部分独立功能的实现所接入的第三方SDK情况。</p>
      <h2 id="导言">导言</h2>
      <p>微信是一款由深圳市腾讯计算机系统有限公司（注册地为深圳市南山区粤海街道麻岭社区科技中一路腾讯大厦35层。以下亦简称“我们”或“腾讯”）提供服务的社交产品（本指引中的“微信”是与其海外运营版本即“WeChat”相区别的产品。微信与WeChat用户以手机号码区分，通过中国大陆地区手机号码进行注册的用户为微信用户，其余则为WeChat用户。本指引中所涉及的个人信息处理活动及保护均针对微信产品）。<strong>为说明微信会如何收集、使用和存储你的个人信息及你享有何种权利，我们将通过本指引向你阐述相关事宜，其中要点如下：</strong></p>
      <p>1. 我们将逐一说明我们收集的你的个人信息类型及其对应的用途，以便你了解我们针对某一特定功能所收集的具体个人信息的类别、使用理由及收集方式。</p>
      <p>2. 当你使用某些功能时，我们会在获得你的同意后，收集你的敏感个人信息。如你在使用推荐通讯录朋友功能时我们会收集你的<strong>手机联系人信息</strong> ，你在使用摇一摇功能时我们会收集你的<strong>精确位置信息</strong>。除非按照相关法律法规要求必须收集，拒绝提供这些信息仅会使你无法使用相关特定功能，但不影响你正常使用微信的其他功能。</p>
      <p>3. 我们不会主动共享或转让你的个人信息至深圳市腾讯计算机系统有限公司外的第三方。如存在其他共享或转让你的个人信息，或你需要我们将你的个人信息共享或转让至深圳市腾讯计算机系统有限公司外的第三方情形时，我们会直接征得或确认第三方征得你对上述行为的明示同意，但法律法规另有规定或因保护用户权益或保护微信生态安全需要除外。此外，我们会对对外提供信息的行为进行风险评估。</p>
      <p>4. 我们不会主动从深圳市腾讯计算机系统有限公司外的第三方获取你的个人信息。如为业务发展需要从第三方获取你的个人信息，我们会依法核实来源的合法性，或在获取前向你明示你个人信息的来源、类型及使用范围，如微信开展业务需进行的个人信息处理活动超出你原本向第三方提供个人信息时的授权同意范围，我们将在处理你的该等个人信息前，征得你的明示同意；此外，我们也将会严格遵守相关法律法规的规定，并要求第三方保障其提供的信息的合法性。</p>
      <p>5. 你可以通过本指引所列途径及方式查阅、更正、复制、删除你的个人信息，也可以撤回同意、注销帐号、投诉举报以及设置<a href="https://ads.privacy.qq.com/ads/wxoptout.html?media_source=101001&amp;info=">个性化广告</a>等隐私功能。</p>
      <p>6. 为了在本指引下收集你的信息，或者向你提供服务、优化我们的服务以及保障你的帐号安全，我们将需要向你索取相关的权限；其中的敏感权限例如<strong>通讯录、位置、摄像头、麦克风、相册</strong>等均不会默认开启，只有在你明确同意后我们才会在你同意的范围内调用或使用。对你已经向我们授权的该等权限，<strong>你也可以在“我—设置—隐私—个人信息与权限—系统权限管理”中查询我们已调用或使用的系统权限类型、使用目的，并进行统一管理。</strong></p>
      <p>需要特别说明的是，我们获得具体某一项敏感权限并不代表我们必然会收集你的相关信息；即使我们已经获得敏感权限，也仅在必要范围内，并在你使用相关的服务或功能期间根据本指引来收集你的相关信息。</p>
      <p>如你想了解更加详尽的信息，请根据以下索引阅读相应章节：</p>
      <p><a href="#1%E6%88%91%E4%BB%AC%E6%94%B6%E9%9B%86%E3%80%81%E4%BD%BF%E7%94%A8%E7%9A%84%E4%BF%A1%E6%81%AF">1. 我们收集、使用的信息</a></p>
