      <p><a href="#4%E4%BF%A1%E6%81%AF%E5%85%B1%E4%BA%AB">4. 信息共享</a></p>
      <p><a href="#5%E4%BD%A0%E7%9A%84%E6%9D%83%E5%88%A9">5. 你的权利</a></p>
      <p><a href="#6%E5%8F%98%E6%9B%B4">6. 变更</a></p>
      <p><a href="#7%E6%9C%AA%E6%88%90%E5%B9%B4%E4%BA%BA%E4%BF%9D%E6%8A%A4">7. 未成年人保护</a></p>
      <p><a href="#8%E5%85%B6%E4%BB%96">8. 其它</a></p>
