      <h2 id="1我们收集、使用的信息">1.我们收集、使用的信息</h2>
      <p>在你使用微信服务的过程中，我们会按照如下方式收集、使用你在使用服务时主动提供或因为使用服务而产生的信息：</p>
      <p>1.1 当你注册微信服务时，我们会收集、使用你的昵称、头像、手机号码，收集这些信息是为了帮助你完成微信注册和使用微信社交等功能，保护你微信帐号的安全。收集、使用手机号码是为了满足相关法律法规的真实身份信息认证要求。若你不提供这类信息，你可能无法正常使用我们的服务。你还可以根据自身需求选择填写性别、地区等信息。此外，你可以选择向我们提供你的<strong>声纹特征值信息</strong>以使用声音锁功能，该信息属于敏感个人信息，拒绝提供该信息仅会使你无法使用声音锁功能，但不影响你正常使用微信的其他功能。</p>
      <p>1.2 当你使用微信服务时，为保障你正常使用我们的服务，维护我们服务的正常运行，改进及优化我们的服务体验以及保障你的帐号安全，我们会收集、使用你的设备型号、操作系统、<a href="https://privacy.qq.com/document/priview/896637b6dc4f4b80b48163580d63e07e">唯一设备标识符</a>（指由设备制造商编入到设备中的一串字符，可用于以独有方式标识相应设备）、登录IP地址、微信软件版本号、接入网络的方式、类型和状态、网络质量数据、设备加速器（如重力感应设备）、操作日志、服务日志信息（如你在微信进行搜索、查看操作的记录、服务故障信息、引荐网址等信息）等日志信息。</p>
      <p>1.3 当你使用微信朋友圈功能时，为了让你的好友可以浏览你分享的内容，我们会收集、使用并按照你设定的规则展示你发布的朋友圈文字、照片、视频、<strong>位置信息</strong>、评论、点赞、朋友圈封面等信息。你也可以随时删除你的上述朋友圈信息。除非经你自主选择或遵从相关法律法规要求，我们不会对外提供上述朋友圈信息，或者将其用于该功能以外的其他用途。你可以在“设置”—“隐私” —“朋友圈”中设置朋友圈内容的可见范围。</p>
      <p>1.4 当你使用微信公众帐号（含订阅号、服务号，以下亦简称“公众号”）时，为了更好地为你提供相关服务（包括基于系统和信息安全、虚假消息鉴别等），以及在“订阅号消息”页面的推荐功能里为了向你提供更加便捷、优质和个性化的服务和内容，我们会收集、使用你的公众号关注、取消关注、互动记录，以及公众号信息内容阅读、观看、收藏、推荐、分享及微信内浮窗管理（加入浮窗/移出浮窗/关闭浮窗）等操作记录。你可以通过点击“订阅号消息”页面右上角的标识进入订阅号列表，点击“订阅号列表”页面右上角“…”—点击页面底部“设置”—点击“订阅号消息个性化推荐”，自行选择关闭订阅号消息中推荐功能的个性化内容推荐服务。</p>
      <p>当你使用订阅号功能时，为了更合理地向你展示你所关注的公众号发布的文章，我们还会使用你的订阅号信息内容阅读、观看等操作记录，对文章进行更优化的排序展示。你也可以通过点击“订阅号消息”页面右上角的标识，进入订阅号列表，切换为根据文章发布时间先后进行排序展示。</p>
      <p>若公众号主体在向你提供服务时需通过微信获取你的个人信息，我们会在征得你的明示同意后，向公众号主体提供你的个人信息，具体提供范围以交互页面中你同意的为准。你也可以在公众号的设置页管理相关信息，如关闭提供你的<strong>位置信息</strong>等。</p>
      <p>1.5 当你使用微信小程序时，为了更好地为你提供相关服务（包括基于系统和信息安全等），微信会收集、使用你的微信小程序登录、浏览、使用、添加到/移除出“我的小程序”、微信内浮窗管理（加入浮窗/移出浮窗/关闭浮窗）等操作记录。</p>
      <p>若开发者在向你提供微信小程序服务时，需通过微信获取你的个人信息，我们会在征得你的明示同意后，向开发者提供你的个人信息或可获取你个人信息的微信接口，具体提供范围以交互页面中你同意的为准。你也可以在小程序设置页管理已通过微信提供至开发者的信息或接口，我们会根据你的指示协助通知开发者删除你的个人信息或在你关闭相应接口后，停止通过该接口向开发者提供你的个人信息。</p>
      <p>1.6 当你使用微信附近的人、摇一摇、雷达加好友、面对面建群以及附近的小程序等功能时，我们会在获得你的自主选择同意后，记录你的<strong>位置信息</strong>，目的是为了向你提供该服务。拒绝提供该信息仅会使你无法使用上述功能，但不影响你正常使用微信的其他功能。</p>
      <p>1.7 当你使用微信运动功能时，我们需要收集并展示你的步数信息，并与你的好友进行比较、点赞互动。拒绝提供这些信息仅会使你无法使用微信运动功能，但不影响你正常使用微信的其他功能。</p>
      <p>1.8 当你使用微信看一看（包括“朋友在看”“热点广场”）功能时，为了向你提供更加便捷、优质和个性化的服务和内容，我们会使用你的公众号、视频号关注、访问、互动，以及内容阅读、播放、点赞、分享、在看、评论等操作记录。你也可以通过点击“看一看”—右上角“个人中心”—页面底部“隐私设置”—“个性化推荐”开关，自行关闭个性化内容推荐服务。当你使用看一看（热点广场）功能时，为了向你提供更精准的位置相关推荐内容，我们会在获得你的自主选择同意后，<strong>收集你的位置信息。</strong></p>
      <p>1.9 当你在微信使用搜索能力（包括搜一搜、公众号搜索、扫一扫、指尖搜索、图片搜索、小程序搜索等）时，为了向你展示更精准的位置相关搜索结果，我们会在获得你的自主选择同意后，<strong>收集你的位置信息</strong> 。拒绝提供该信息可能会使你无法使用上述功能，或无法达到上述基于位置信息提供的相关服务拟达到的效果，但不影响你正常使用微信的其他功能。同时，为了向你展示更精准匹配的搜索结果，我们会使用你的搜索记录、小程序访问记录、公众号关注，以及公众号文章阅读、收藏、分享、在看等操作记录，你也可以通过点击“发现”—“搜一搜”—页面底部“隐私设置”—“个性化搜索结果展示”开关，自行关闭个性化搜索结果展示服务。</p>
      <p>1.10 当你使用微信语音输入、语音与文字间转换、对聊天内容（包括文字信息和图片）使用搜一搜功能以及使用微信内翻译功能（包括翻译聊天、朋友圈文字信息及翻译图片中的文字，以及使用边写边译功能）、扫一扫功能时，我们会收集你的语音内容、待转换的语音或文字信息、待搜索的信息、待翻译的信息和扫描的内容，收集是为实现上述功能所必需的。我们实时处理之后，向你返回处理结果，不会保存你的数据。</p>
      <p>1.11 当你在微信内充值、使用微信豆的，我们会收集你的充值<strong>订单信息、使用记录</strong>，这是为你提供该功能所必需的。如需了解微信豆的更多信息，你可以进一步查阅<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=wxbean&amp;lang=zh_CN">《微信豆及相关功能使用条款》</a>。</p>
      <p>1.12 微信支付功能由财付通支付科技有限公司（以下亦简称“财付通公司”）向你提供服务。当你开通微信支付功能时，<strong>财付通公司会收集你的姓名、身份证号码或其他合法身份证明文件号码、银行卡类型及卡号、有效期及银行预留手机号</strong>；当你使用微信支付时， <strong>财付通公司还会收集你的相关交易订单、支付记录以便于你查询</strong>。拒绝提供该信息仅会使你无法使用微信支付功能，但不影响你正常使用微信的其他功能。如需进一步了解微信支付功能的隐私政策，你可以进一步查阅<a href="https://www.tenpay.com/v3/helpcenter/low/privacy.shtml">《财付通隐私政策》</a>。</p>
      <p>1.13 当你添加企业微信好友，加入企业微信互通群或使用其他企业微信互通功能时，你的企业微信好友所在的企业主体（“企业微信用户”）可能会收集以下信息：包括你的昵称、头像等帐号信息，你和企业微信好友之间的聊天记录以及其他你主动向企业微信用户披露的数据信息。<strong>你在选择使用企业微信互通服务时，可通过产品交互提示或企业用户展示的数据保护政策了解你的权益。你可以拒绝使用该等互通服务，但不影响你正常使用微信的其他功能</strong>。如需进一步了解企业微信的隐私政策，你可以进一步查阅<a href="https://work.weixin.qq.com/nl/privacy">《企业微信隐私保护指引》</a>。</p>
      <p>1.14 当你使用微信与其他软件或硬件互通功能时，其他软件或硬件的提供方在经你同意后可以获取你在微信主动提供、公开或传输的相关信息，你在选择前应充分了解其他软件或硬件的产品功能及个人信息处理规则。</p>
      <p>1.15 当你使用微信视频号浏览视频内容和直播内容时，为了优化你的使用体验、让你获得个性化的内容，我们会使用你的关注、点赞、收藏（星标）、互动、搜索、浏览及转发分享的记录。你可以在“浏览设置”中点击“隐私”选项，点击“个性化推荐”分别对视频内容或直播内容关闭个性化内容推荐。</p>
      <p>为实现向你推荐本地内容或推荐同城直播等目的，我们会在获得你的自主选择同意后，<strong>收集你的位置信息</strong>进行相关推荐。当你使用微信视频号功能时若你不同意或取消授权，与位置信息相关的功能可能无法使用，但不会影响你使用其他功能。</p>
      <p>你点赞的视频内容也将被推荐给你的微信好友，你可以取消点赞，取消后不会再推荐给你的微信好友。你也可以取消关注的视频号及收藏（星标）的视频内容。当你使用直播预约功能时，我们将会收集你的预约记录，以提醒你及时观看直播，你可以取消预约。</p>
      <p>1.16 <strong>当你评论、私信、点赞或关注视频号用户，以及你正在观看或打赏视频号用户的直播时，你的微信个人名片会向该视频号用户展示，你的头像、昵称、及评论还会向其他用户展示。</strong> 你可以选择用微信号身份或视频号身份或设置一个新身份观看直播或私信互动，也可以设置你不看的视频号，不看部分微信好友点赞的内容，或不让部分微信好友看自己点赞的内容。<strong>你可以进入“互动历史”选项查阅你关注、点赞、收藏（星标）的视频内容以及消息和私信。</strong></p>
      <p>1.17 当你创建微信视频号帐号并发布内容，你设置的视频号名字、头像、性别、地区、简介、封面、认证信息、黑名单帐号、绑定的公众号或企业微信，你发布的视频、直播内容、图片、文字、<strong>位置</strong>、链接，以及与你有关的评论、互动、消息通知等信息将被收集，同时，当你发布视频内容或直播内容，我们会根据你希望使用的功能依场景请求你授权相应功能所需的<strong>相机、照片、麦克风或位置权限</strong>，若你不同意或取消授权，将无法使用相应功能，但不影响你使用微信其他功能。你可以更改你的视频号的名字、头像、性别、简介、封面、黑名单帐号等信息，设置为私密帐号，删除你发表的动态。在视频号的设置页面，你可以选择在微信个人名片中展示你的视频号内容，你也可以选择关闭展示。</p>
      <p>1.18 当你发表视频内容、直播内容、绑定公众号时，根据相关法律法规以及校验身份的需要，你需要进行<strong>实名认证</strong>。<strong>实名认证</strong>服务由财付通公司提供。如果拒绝<strong>实名认证</strong>，你将可能无法获得相关服务，但不影响其他功能的正常使用。此外，<strong>为保障帐号安全，我们可能需要你填写你的真实姓名、身份证号码并通过“人脸识别”的方式进行校验以确认是你本人操作，我们仅获得校验的结果，不会保留相关信息，除非腾讯依照法律法规规定另行取得你的同意。上述信息包含敏感个人信息，你可以拒绝提供，如果拒绝提供你将可能无法获得相关服务，但不影响其他功能的正常使用。你发表的视频内容、直播内容若标记了位置信息，将会被推荐给该位置附近的已授权位置权限的用户、同城用户等。</strong></p>
      <p>1.19 当你对视频号帐号进行帐号认证、管理直播收入、与机构用户进行绑定、参加直播任务或者参加我们主办的各类活动，可能会涉及对你的信息进行处理，我们将在相关场景向你另行告知相应的规则并征得你的同意。当你使用视频号进行游戏直播或小游戏直播时，我们还会按照<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=finder_live_game_agreement&amp;lang=zh_CN">《微信视频号游戏直播须知》</a>处理你的信息。</p>
      <p>1.20 你可以在你的视频号主页进入“隐私设置”，选择“个人信息浏览与导出”，以对你的帐号信息、认证信息等进行查阅，并按照指引导出相关信息。</p>
      <p>1.21 当你授权微信游戏（微信游戏是指微信为用户提供的游戏相关服务与功能，包括“发现—游戏”模块，“小游戏”类目的小程序，以及“微信游戏”公众号）使用你的游戏账号信息（包括登录信息、角色信息、对局数据）、游戏好友关系的，我们将在微信游戏的好友排行、战绩服务、游戏社区、内容与服务推荐等场景使用该等信息，<strong>你可以选择该等信息的可见范围，也可以选择不向好友展示这些信息，不展示并不影响你正常使用腾讯游戏、微信游戏和微信的其他功能</strong>。</p>
      <p>1.22 当你使用“小游戏”类目的小程序（下亦简称“小游戏”）时，为了更好地为你提供相关服务，微信会使用你的小游戏登录、浏览、添加到/移除出“我的小程序”、 微信内浮窗管理（加入浮窗/移出浮窗/关闭浮窗）等操作记录。<strong>我们会在微信游戏相关界面向你的好友展示你玩过的小游戏、你的头像和昵称等。你可以选择不向好友展示这些信息，不展示并不影响你正常使用小游戏和微信的其他功能。</strong></p>
      <p>1.23 你可以在微信游戏设置你的微信游戏名片。设置微信游戏名片的过程中，我们会收集你为此功能所主动填写/设置的头像、昵称、生活照、签名、性别、生日、<strong>地区（此处会向你申请获取你的位置以快速展示你所在地区，你也可以拒绝授权从而不展示地区）等信息</strong>。</p>
      <p>当你成功设置后，你所填写/设置的前述信息将仅用于微信游戏内的微信游戏名片功能，以便你更好地与其他用户互动（其他用户将可通过微信游戏名片来关注你或通过 “打招呼”功能与你互动）及获得个性化的游戏礼包（如生日礼包）；且该等信息不会被用于其他场景，也不会被用于判定微信账号的使用权权属与真实身份信息认证等。<strong>你可以选择不设置你的微信游戏名片，这不会影响你正常使用微信游戏中心、腾讯游戏、小游戏的其他功能。</strong></p>
      <p>1.24 <strong>当你使用状态功能时，我们会收集你发表的内容、自主选择的状态图标与位置等信息，以向其他用户展示，这是实现这一功能所必需的。我们会在具体状态信息的展示期内（状态展示有效期请见状态设置界面中的提示），根据你公开与否的选择，对该等信息进行相应的展示，你也可以随时删除状态以删除这些信息。除非经你自主选择或遵从相关法律法规要求，我们不会对外提供上述状态信息，或者将其用于该功能以外的其他用途。</strong></p>
      <p>1.25 当你使用微信的收藏功能来储存各类信息时，该等信息将被上传并存储在我们的服务器中，以便你在其他设备上登录微信时使用该等信息，这是实现收藏功能所必需的。我们会对你收藏的信息以加密的方式存储，你也可以随时删除自己所收藏的信息。除非经你自主选择或遵从相关法律法规要求，我们不会对外提供上述信息，或者将其用于该功能以外的其他用途。</p>
      <p>1.26 当你使用卡包功能时，为向你推荐附近与卡券关联的本地服务或便于你使用所在城市的公交卡服务，我们会在获得你的同意后，收集你的<strong>位置信息</strong>。拒绝提供该信息可能会使你无法使用上述功能，或无法达到上述基于位置信息提供的相关服务拟达到的效果，但不影响你正常使用微信的其他功能。</p>
      <p>1.27 你可以在你的个人信息栏中填写性别、<strong>地区</strong>、个性签名、<strong>地址</strong>等，这些信息将被上传并存储在我们的服务器中，以便你在其他设备上登录微信时使用该等信息。你也可以随时修改、删除该等信息。</p>
      <p>1.28 当你使用微信群聊功能时，群聊名称、群公告、群成员昵称、群设置、备注、你设置的聊天背景图等信息会存储在我们的服务器中，这是实现这一功能所必需的。你的头像、昵称等还会向群成员展示。</p>
      <p>1.29 <strong>我们不会收集你的聊天记录。你在微信中的聊天记录会存储在你的终端设备，你可以选择自行备份、删除或在不同设备中转移。</strong></p>
      <p>1.30 微信服务中的第三方服务由外部第三方主体提供，微信无法获得你在使用该类第三方服务时产生的信息。但是，如果你已明示同意该第三方获得你的<strong>位置信息</strong>，该第三方将通过微信接口获得相关信息。拒绝提供该信息仅会使你无法使用上述第三方服务，但不影响你正常使用微信的功能。</p>
      <p>1.31 如果你联系我们的客服，我们可能需要你提供必要的个人信息进行身份验证，以保证你的帐号安全。为根据你的诉求提供服务，经过你的授权，人工客服人员需要在你授权范围内查询或核验你的相关信息，我们将尽可能采取技术和管理措施保障你的信息安全和保密，并在必要范围内进行使用。<strong>我们可能还会保存你的联系方式（你使用的或主动提供的手机号码、微信号、QQ号或其他联系方式）、你与我们的通信记录和内容，以及其他必要信息，以便为你提供和记录客户服务</strong>。</p>
      <p>1.32 <strong>另外，根据相关法律法规及国家标准，以下情形中，我们可能会处理你的相关个人信息而无需征求你的授权同意：</strong></p>
      <p><strong>a)为个人信息处理者履行法定义务或法定职责所必需；</strong></p>
      <p><strong>b)为订立、履行你作为一方当事人的合同所必需；</strong></p>
      <p><strong>c)为应对突发公共卫生事件，或者紧急情况下为保护自然人的生命健康和财产安全所必需；</strong></p>
      <p><strong>d)依法在合理的范围内处理你自行公开或者其他已经合法公开的个人信息；</strong></p>
      <p><strong>e)法律、行政法规规定的其他情形。</strong></p>
      <p>1.33 请你理解，我们向你提供的功能和服务是不断更新和发展的，如果某一功能或服务未在前述说明中告知收集、使用你的信息的处理规则，我们会通过页面提示、交互流程、网站公告等方式另行向你告知信息收集的范围、目的及方式，以征得你的同意。</p>
      <p>1.34 <strong>我们可能将通过某些功能所收集的信息用于我们的其他服务。例如，我们可能将你在使用我们某一功能或服务时我们收集的信息，在另一功能或服务中用于向你或你的好友提供特定内容，包括但不限于展示广告（你可以通过“我—设置—隐私—个人信息与权限—个性化广告管理”管理微信为你提供的<a href="https://ads.privacy.qq.com/ads/wxoptout.html?media_source=101001&amp;info=">个性化广告</a>），对你阅读过的内容进行信息安全类提示，基于特征标签进行间接人群画像并提供更加精准和个性化的服务和内容，以及保护用户权益或保护微信生态安全等</strong> 。</p>
      <p>1.35 为了确保服务的安全，帮助我们更好地了解我们应用程序的运行情况，我们可能记录相关信息，例如，你使用应用程序的频率、崩溃数据、总体使用情况、性能数据以及应用程序的来源。我们不会将我们存储在分析软件中的信息与你在应用程序中提供的任何个人身份信息融合处理。</p>
      <p>1.36 如我们使用你的个人信息，超出了你所授权的目的及具有直接或合理关联的范围，我们将在使用你的个人信息前，再次向你告知并征得你的明示同意。</p>
      <p>1.37 请你注意，目前微信不会主动从深圳市腾讯计算机系统有限公司外的第三方获取你的个人信息。如未来为业务发展需要从第三方间接获取你个人信息，我们会在获取前向你明示你个人信息的来源、类型及使用范围，如微信开展业务需进行的个人信息处理活动超出你原本向第三方提供个人信息时的授权同意范围，我们将在处理你的该等个人信息前，征得你的明示同意。</p>
      <p>此外，我们将会严格遵守相关法律法规的规定，并要求第三方保障其提供的信息的合法性。</p>
      <h2 id="2信息的存储">2.信息的存储</h2>
      <p><strong>2.1 信息存储的地点</strong></p>
      <p><strong>我们会按照法律法规规定，将境内收集的用户个人信息存储于中国境内</strong>。如果你的个人信息存储地点从中国境内转移到境外的，我们将严格依照法律的规定执行。</p>
      <p><strong>2.2 信息存储的期限</strong></p>
      <p>一般而言，我们仅在为实现目的所必需的时间及法律、行政法规所规定的保存期限内保留你的个人信息，例如：</p>
      <p>手机号码：无论你是通过手机号码注册还是已有微信帐号辅助注册新微信帐号，若你需要使用微信服务，我们需要一直保存你的手机号码（包括初始注册手机号码、绑定的手机号码等），以保证你正常使用该服务。当你注销微信帐号后，我们将依法删除相应的信息；</p>
      <p>朋友圈信息：当你发送了朋友圈信息，我们需要保存你的朋友圈信息，以保证你正常使用朋友圈功能，当你删除你的朋友圈信息后，我们将删除相应的信息。</p>
      <p>当我们的产品或服务发生停止运营的情形时，我们将以推送通知、公告等形式通知你，并在合理的期限内删除你的个人信息或进行匿名化处理。（本指引中的“匿名化处理”，是指通过对个人信息的技术处理，使得个人信息主体无法被识别，且处理后的信息不能被复原的过程。个人信息经匿名化处理后所得的信息不属于个人信息。）</p>
      <h2 id="3信息安全">3.信息安全</h2>
      <p>我们努力为用户的信息安全提供保障，以防止信息的丢失、不当使用、篡改、未经授权访问或披露。</p>
      <p>我们将在合理的安全水平内使用各种安全保护措施以保障信息的安全。例如，我们会使用加密技术（例如，SSL）、去标识化、匿名化处理等手段来保护你的个人信息。</p>
      <p>我们通过不断提升的技术手段加强安装在你设备端的软件的安全能力，以防止你的个人信息泄露。例如，我们为了安全传输会在你设备本地完成部分信息加密的工作；为了预防病毒、木马程序或其他恶意程序、网站，我们可能会了解你设备安装的应用信息、正在运行的进程信息或设备内存中寄存的数据；为了预防诈骗、盗号、仿冒他人等不法行为和进行安全检查可能会分析利用唯一设备标识符、登录IP地址、操作日志、<strong>位置信息</strong>等数据，以便于采取安全措施或进行安全提醒等。</p>
      <p>我们建立专门的管理制度、操作规程和组织以保障信息的安全。例如，我们严格限制访问信息的人员范围，要求他们遵守保密义务，并进行审计。此外，腾讯还会设立主要由外部成员组成的独立机构对我们的个人信息保护情况进行监督。</p>
      <p>若发生个人信息泄露等安全事件，我们会启动应急预案及补救措施，阻止安全事件扩大，并依法履行相关的报告及通知义务。</p>
      <p>目前，微信通过了国家网络安全等级保护（三级）的定级、备案及年度测评，在信息安全方面已达到ISO27001、国际信息安全管理体系、TRUSTArc等国际权威认证标准的要求，并已获得了相应的认证。</p>
      <p>我们会尽力保护你的个人信息。<strong>我们也请你理解，任何安全措施都无法做到无懈可击</strong>。</p>
      <h2 id="4信息共享">4.信息共享</h2>
      <p>4.1 为了投放广告，评估、优化广告投放效果等目的，我们需要向广告主及其代理商等第三方合作伙伴共享你的部分数据，要求其严格遵守我们关于数据隐私保护的措施与要求，包括但不限于根据数据保护协议、承诺书及相关数据处理政策进行处理，避免识别出个人身份，保障隐私安全。具体可见<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=ad_shared_info_list&amp;lang=zh_CN">《广告信息共享清单》</a>。</p>
      <p>4.2 我们不会向合作伙伴分享可用于识别你个人身份的信息，除非你明确授权。</p>
      <p>4.3 当你在第三方主体运营的网站或者应用中使用微信登录功能时，我们将向该等第三方主体提供你的个人信息，具体的对外提供范围以交互页面中你确认的为准。除此之外，在未征得你的明示同意前，我们不会向该等第三方主体提供你的个人信息。<strong>你可以通过“我—设置—隐私—个人信息与权限—授权管理”查看你已授权的应用清单，并管理、解除你授权的信息。</strong></p>
      <p>4.4 为确保部分独立功能的实现，使你能够使用和享受更多的服务及功能，我们会在应用中嵌入第三方的SDK，我们将审慎评估该等SDK的使用目的。了解更多有关SDK处理个人信息的目的、方式及范围等信息，请点击<a href="https://weixin.qq.com/cgi-bin/readtemplate?t=3rdsdk_list&amp;lang=zh_CN">《第三方SDK目录》</a>。</p>
      <p>随着我们业务的持续发展，我们有可能进行合并、分立等交易，若涉及个人信息转移的，我们将告知你相关情形，按照法律法规及不低于本指引所要求的标准继续保护或要求新的处理者继续保护你的个人信息。</p>
      <h2 id="5你的权利">5.你的权利</h2>
      <p>在你使用微信期间，为了你可以更加便捷地查阅、复制、更正、删除你的个人信息，同时保障你撤回对个人信息处理的同意及注销帐号的权利，我们在产品设计中为你提供了相应的操作设置，你可以参考下面的指引进行操作。此外，我们还设置了投诉举报、请求解释的渠道，你的意见及请求将会得到及时的处理。</p>
      <p><strong>请注意，不同操作系统、微信软件的不同版本之间，操作设置可能存在差异</strong>；此外，为了优化你的使用体验，我们也可能对操作设置进行调整。故如下指引仅供参考，若你对行使相关权利的方式及途径仍有任何疑问的，你可以通过本规则第9条披露的方式与我们联系。</p>
      <p><strong>5.1 查阅、复制个人信息</strong></p>
      <p><strong>5.1.1 查阅头像、昵称（即“名字”，下同）、拍一拍、微信号、二维码、性别、地区、个性签名、我的地址等基本信息：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击头像栏；<br> 3) 进行信息查询访问。<br></p>
      <p><strong>5.1.2 查阅微信号、手机号、微信密码、声音锁、应急联系人、登录设备管理、更多安全设置（QQ号、邮箱地址）：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击“设置”；<br> 3) 点击“帐号与安全”；<br> 4) 进行信息查询访问。<br></p>
      <p><strong>5.1.3 查阅朋友圈信息、状态信息：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击“朋友圈”；<br> 3) 点击“我的朋友圈”以访问朋友圈信息，或点击“状态”访问状态信息。<br></p>
