      <p>1) 进入微信后，点击“发现”；<br> 2) 点击“游戏”；<br> 3) 点击头像；<br> 4) 点击设置图标；<br> 5) 点击“新消息通知”“订阅管理”“隐私”“按游戏管理”等进行相应访问和设置。<br></p>
      <p><strong>5.1.5 查阅通讯录黑名单：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击“设置”；<br> 3) 点击“隐私”；<br> 4) 点击“通讯录黑名单”进行查询。<br></p>
      <p><strong>5.1.6 查阅在视频号的互动信息：</strong></p>
      <p>1) 进入微信后，点击“发现”；<br> 2) 点击“视频号”；<br> 3) 点击右上角人形图标；<br> 4) 点击“隐私”；<br> 5) 点击“互动历史”。<br></p>
      <p><strong>5.1.7 查阅及导出视频号帐号个人信息：</strong></p>
      <p>1) 进入微信后，点击“发现”；<br> 2) 点击“视频号”；<br> 3) 点击右上角人形图标；<br> 4) 点击视频号头像名字一栏，进入视频号主页；<br> 5) 点击右上角“...”，进入设置页；<br> 6) 点击“隐私设置”；<br> 7) 点击“个人信息浏览与导出”。<br></p>
