      <p>1) 进入微信后，点击“我”；<br> 2) 点击“设置”；<br> 3) 点击“隐私”；<br> 4) 点击“个人信息与权限”<br> 5) 点击“个人信息”以查阅你的个人信息；<br> 6) 点击“导出个人信息”，以复制一份个人信息副本并发送到你指定的邮箱。<br></p>
      <p><strong>5.2 删除个人信息</strong></p>
      <p><strong>5.2.1 删除一对一全部聊天记录：</strong></p>
      <p>1) 进入微信后，打开对话，点击右上角“…”图标；<br> 2) 点击清空聊天记录。<br></p>
      <p><strong>5.2.2 删除群聊天记录：</strong></p>
      <p>1) 进入微信后，打开对话，点击右上角“…”图标；<br> 2) 点击清空聊天记录。<br></p>
      <p><strong>5.2.3 删除部分/全部聊天记录：</strong></p>
