      <p>1) 进入微信后，点击“我”；<br> 2) 点击头像栏；<br> 3) 删除相关信息。<br></p>
      <p><strong>5.2.5 删除朋友圈：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击“朋友圈”；<br> 3) 点击“我的朋友圈”；<br> 4) 点击需要访问的朋友圈信息；<br> 5) 点击右上角“…”，并删除朋友圈信息。<br></p>
      <p><strong>5.2.6 删除视频号内容：</strong></p>
