      <p><strong>5.4.4 关闭摇一摇：</strong></p>
      <p>1) 进入微信后，点击“我”；<br> 2) 点击“设置”；<br> 3) 点击“通用”；<br> 4) 点击“发现页管理”；<br> 5) 关闭“摇一摇”。<br></p>
