      <p>1) 在允许使用微信个人帐号进行登录的网站或者应用中，点击“微信登录”或者其他指代该意义的图标（通常是微信图标）；<br> 2) 选择不同的个人信息（头像、昵称），或者新建用户信息（头像、昵称），来作为登录该等网站或者应用的头像和昵称。<br></p>
