      <p><strong>5.8 逝者近亲属的权利</strong></p>
      <p>如微信用户不幸逝世，其近亲属可通过腾讯客服官方渠道联系我们，以获取相关指引来行使法律规定的合法、正当权益。</p>
      <h2 id="6变更">6.变更</h2>
      <p>我们可能会适时对本指引进行修订。当指引的条款发生重大变更时，我们会在你登录及版本更新时以推送通知、弹窗或其他符合法律要求的适当形式向你展示变更后的指引。</p>
      <h2 id="7未成年人保护">7.未成年人保护</h2>
      <p><strong>我们非常重视对未成年人个人信息的保护。根据相关法律法规的规定，若你是18周岁以下的未成年人，在使用微信服务前，应事先取得你的家长或法定监护人的书面同意。若你是未成年人的监护人，当你对你所监护的未成年人的个人信息有相关疑问时，请通过第9章中的联系方式与我们联系。</strong></p>
      <p><strong>特别地，若你是14周岁以下的儿童，我们还专门为你制定了<a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a> ，儿童及其监护人在为14周岁以下的儿童完成帐号注册前，还应仔细阅读腾讯公司专门制定的<a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a> 。只有在取得监护人对<a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a>的同意后，14周岁以下的儿童方可使用微信服务。</strong></p>
      <h2 id="8其他">8.其他</h2>
      <p>8.1 腾讯<a href="https://privacy.qq.com/policy/tencent-privacypolicy">《隐私政策》</a>是腾讯统一适用的一般性隐私条款，其中所规定的用户权利及信息安全保障措施均适用于微信用户。如腾讯<a href="https://privacy.qq.com/policy/tencent-privacypolicy">《隐私政策》</a>与本指引存在不一致或矛盾之处，请以本指引为准。</p>
      <p>8.2 在微信某些具体服务或功能项下，我们可能还会通过交互文案、产品界面或弹窗等方式向你另行告知处理个人信息的目的、方式或范围，若另行告知的内容与本指引不一致的，以另行告知的内容为准。</p>
      <p>8.3 <a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a>是适用于14周岁以下的儿童用户的信息保护规则。在<a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a>的适用范围内，如其与腾讯<a href="https://privacy.qq.com/policy/tencent-privacypolicy">《隐私政策》</a>、本指引存在不一致或矛盾之处，请以<a href="https://privacy.qq.com/privacy-children.htm">《儿童隐私保护声明》</a>为准。</p>
